<template>
  <div class="list-and-btn">
    <div class="list" :class="{ 'float-in': doAnimation }">
      <div class="listarea-content">
        <OptionBox
          ref="option"
          :cur-score="curScore"
          :good-option="goodOption"
          :bad-option="badOption"
          :notice="showNotice"
        ></OptionBox>
      </div>
      <div class="textarea-content">
        <SuggestionBox ref="suggestion" :cur-score="curScore"></SuggestionBox>
      </div>
    </div>
    <div class="btn-content">
      <button class="btn" @click="submit"></button>
    </div>
  </div>
</template>

<script>
import OptionBox from "./OptionBox";
import SuggestionBox from "./SuggestionBox";

export default {
  components: {
    OptionBox,
    SuggestionBox,
  },
  data() {
    return {
      doAnimation: true,
      showNotice: false,
    };
  },
  props: {
    curScore: Number,
    good: String,
    bad: String,
  },
  computed: {
    goodOption() {
      return this.good.split("&&");
    },
    badOption() {
      return this.bad.split("&&");
    },
  },
  watch: {
    curScore(newV, oldV) {
      if (newV !== oldV) {
        this.showNotice = false;
        this.doAnimation = false;
        setTimeout(() => {
          this.doAnimation = true;
        }, 10);
      }
    },
  },
  methods: {
    submit() {
      const { positiveRes, negativeRes } = this.$refs.option;
      const suggestion = this.$refs.suggestion.suggestion;
      if (!positiveRes.length && !negativeRes.length) {
        //
        this.showNotice = true;
      } else {
        this.$emit("showThankyou", {
          positiveRes,
          negativeRes,
          suggestion,
        });
      }
    },
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

.list {
  width: 100%;
  padding: 0 30px;
  max-height: 300px;
  transition: max-height 0.3s ease-in-out;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translateY(10px);
  opacity: 0;
}

.btn-content {
  flex-shrink: 0;
  margin-top: 24px;
  margin-right: 28px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.btn {
  opacity: 0;
  transform: translateY(10px);
  animation: btn_show 0.3s ease-in-out forwards;
  padding: 4px 10px;
  min-width: 80px;
  color: white;
  font-weight: 400;
  font-size: 14px;
  font-family: PingFangSC-Regular, sans-serif;
  line-height: 22px;
  border: none;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #2f88ff;
}

@keyframes btn_show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.textarea-content,
.listarea-content {
  margin-bottom: 30px;
}

.float-in {
  animation: float_in 0.3s ease-in-out forwards;
}

@keyframes float_in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: none;
  }
}
</style>
