<script>
import EmojiBox from './components/EmojiBox'
import ScoreDetail from './components/ScoreDetail'
import ThankYou from './components/ThankYou'
import axios from 'axios'

const isProd = process.env.NODE_ENV === 'production'
const baseUrl = isProd ? 'https://ide.quickapp.cn' : 'http://localhost:4040'
const COMMENT_URL = `${baseUrl}/api/feedback`

export default {
  name: 'FeedBack',
  components: {
    EmojiBox,
    ScoreDetail,
    ThankYou
  },
  data() {
    return {
      showThankyou: false,
      hideBox: false,
      curScore: '',
      showBox: false
    }
  },
  props: {
    projectId: {
      type: String,
      default: ''
    },
    good: {
      type: String,
      default: '&&&&&&&&&&'
    },
    bad: {
      type: String,
      default: '&&&&&&&&&&'
    },
    titleText: {
      type: String,
      default: '?'
    },
    bottom: {
      type: String,
      default: '40px'
    },
    right: {
      type: String,
      default: '96px'
    },
    visiblity: {
      type: String,
      default: 'hide'
    },
    btn: {
      type: String,
      default: 'show'
    }
  },
  watch: {
    visiblity: {
      immediate: true,
      handler(newV) {
        if (newV === 'show') {
          this.showBox = true
          this.init()
        } else {
          this.showBox = false
        }
      }
    }
  },
  methods: {
    selectScore(msg) {
      this.curScore = parseInt(msg.score)
    },
    doShowThankyou(params) {
      this.showThankyou = true
      //
      const data = {
        projectId: this.projectId,
        rating: this.curScore,
        positive: JSON.stringify(params.positiveRes),
        negative: JSON.stringify(params.negativeRes),
        suggestion: params.suggestion
      }
      this.doSubmit(data)
      setTimeout(() => {
        this.hideBox = true
        this.showBox = false
      }, 2000)
    },
    onBtnClick() {
      this.showBox = !this.showBox
      this.init()
    },
    init() {
      this.showThankyou = false
      this.hideBox = false
      this.curScore = ''
    },
    doSubmit(params) {
      console.log(params, process.env.NODE_ENV)
      axios({
        method: 'post',
        url: COMMENT_URL,
        data: {
          ...params
        }
      })
        .then(response => {
          console.log(response, 'success')
        })
        .catch(error => {
          console.log(error, 'error')
        })
    }
  }
}
</script>

