<template>
  <div>
    <div class="title-wrap">
      <div class="point-wrap">
        <div class="point"></div>
      </div>
      <div class="listarea-title">{{ optionTitle }}</div>
    </div>
    <div class="checkbox-group" v-if="curScore > 3">
      <div
        class="checkbox-item-positive"
        :class="{
          'checkbox-item-positive-checked': positiveRes.includes(item),
        }"
        v-for="(item, index) in options"
        :key="index"
        @click="chosePositive(item)"
      >
        {{ item }}
      </div>
    </div>
    <div class="checkbox-group" v-else>
      <div
        class="checkbox-item-negative-content"
        v-for="(item, index) in options"
        :key="index"
      >
        <div
          class="checkbox-item-negative-line"
          style="display: flex; align-items: center"
          @click="choseNegative(item)"
        >
          <img style="width: 16px; height: 16px" :src="calIcon(item)" />
          <div class="checkbox-item-negative">{{ item }}</div>
        </div>
        <input
          v-if="negativeRes.find((elem) => elem.comment === item)"
          class="checkbox-item-negative-input"
          rows="1"
          placeholder=""
          minlength="0"
          maxlength="Infinity"
          @change="changeCommentDetail($event, item)"
        />
      </div>
    </div>
    <div class="notice-wrap" v-if="showNotice">
      <div class="notice"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      positiveRes: [],
      negativeRes: [],
      showNotice: false,
    };
  },
  props: {
    curScore: Number,
    goodOption: Array,
    badOption: Array,
    notice:Boolean
  },
  computed: {
    optionTitle() {
      return this.curScore > 3
        ? ""
        : this.curScore === 3
        ? ""
        : "";
    },
    options() {
      if (this.curScore > 3) return this.shuffle(this.goodOption);
      else return this.shuffle(this.badOption);
    },
    calIcon(item) {
      console.log(item);
      return (item) => {
        const idx = this.negativeRes.findIndex((elem) => elem.comment === item);
        if (idx != -1) {
          return "https://statres.quickapp.cn/quickapp/ide/feedback/chose.svg";
        } else {
          return "https://statres.quickapp.cn/quickapp/ide/feedback/unchose.svg";
        }
      };
    },
  },
  watch: {
    curScore(newV, oldV) {
      if (newV !== oldV) {
        this.positiveRes = [];
        this.negativeRes = [];
        this.showNotice = false;
      }
    },
    notice:{
      immediate:true,
      handler(newV){
        this.showNotice = newV
      }
    }
  },
  methods: {
    shuffle(arr) {
      //
      for (let i = arr.length - 1; i >= 0; i--) {
        const randomIdx = Math.floor(Math.random() * (i + 1)); //0randomIdxi
        const itemAtIdx = arr[randomIdx];
        arr[randomIdx] = arr[i];
        arr[i] = itemAtIdx;
      }
      return arr;
    },
    chosePositive(item) {
      if (this.positiveRes.includes(item)) {
        this.positiveRes.splice(this.positiveRes.indexOf(item), 1);
      } else {
        this.positiveRes.push(item);
        this.showNotice = false;
      }
    },
    choseNegative(item) {
      const idx = this.negativeRes.findIndex((elem) => elem.comment === item);
      if (idx != -1) {
        this.negativeRes.splice(idx, 1);
      } else {
        this.negativeRes.push({
          comment: item,
          detail: "",
        });
        this.showNotice = false;
      }
    },
    changeCommentDetail(e, comment) {
      const item = {
        comment,
        detail: e.target.value,
      };
      const idx = this.negativeRes.findIndex(
        (elem) => elem.comment === item.comment
      );
      if (idx != -1) {
        this.negativeRes.splice(idx, 1, item);
      }
    },
  },
};
</script>

