<template>
  <div class="emoji-box" @mouseleave="handleLeave()">
    <EmojiItem
      v-for="(item, index) in emojiMsg"
      :key="index"
      :msg="item"
      :cur-score="curScore"
      :cur-hover="curHover"
      @enter="handleEnter"
      @selectScore="selectScore"
      ref="emojiItem"
    ></EmojiItem>
  </div>
</template>

<script>
import { emojiMsg } from "../helper/const";
import EmojiItem from "./EmojiItem";

export default {
  components: {
    EmojiItem,
  },
  data() {
    return {
      emojiMsg: emojiMsg,
      curScore: "", //
      curHover: "", //hover
    };
  },
  mounted() {
    this.$refs.emojiItem.forEach((ele, index) => {
      setTimeout(() => {
        ele.emojiShow = true;
      }, index * 100);
    });
  },
  methods: {
    handleEnter(score) {
      this.curHover = score;
    },
    handleLeave() {
      this.curHover = "";
    },
    selectScore(msg) {
      this.curScore = msg.score;
      this.$emit('selectScore',msg)
    },
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

.emoji-box {
  display: flex;
  width: 300px;
  margin: 0 auto;
  padding: 20px 0;
  justify-content: space-between;
}
</style>
