<template>
  <div>
    <div class="textarea-title">{{suggestionTitle}}</div>
    <textarea
      placeholder=""
      rows="3"
      minlength="0"
      maxlength="Infinity"
      class="textarea"
      v-model="suggestion"
    ></textarea>
  </div>
</template>

<script>
export default {
  data(){
    return{
      suggestion:''
    }
  },
  props:{
    curScore:Number
  },
  watch:{
    curScore(newV, oldV) {
      if (newV !== oldV) {
        this.suggestion = ''
      }
    },
  },
  computed:{
    suggestionTitle(){
      return this.curScore > 3 ? '' : ''
    }
  }
};
</script>

