<template>
  <div
    class="emoji-item"
    :class="{'emoji-show':emojiShow}"
    @mouseenter="handleMouseEnter"
    @click="handleClick"
  >
    <img class="emoji-item-img" :src="imgSrc" />
    <div class="emoji-item-label-content">
      <div class="emoji-item-label">{{ msg.label }}</div>
    </div>
  </div>
</template>

<script>
import { imgSrcPrefix } from "../helper/const";

export default {
  data() {
    return {
      emojiShow:false
    };
  },
  props: {
    msg: Object,
    curScore: String,
    curHover: String,
  },
  computed: {
    imgSrc() {
      //
      if (this.curScore==='' && this.curHover==='') {
        return imgSrcPrefix + this.msg.score + ".png";
      } else if (this.msg.score === this.curScore) {
        return imgSrcPrefix + this.msg.score + ".png";
      } else if (this.msg.score === this.curHover) {
        return imgSrcPrefix + this.msg.score + ".gif";
      } else {
        return imgSrcPrefix + this.msg.score + "-grey.png";
      }
    },
  },
  methods: {
    handleMouseEnter() {
      this.$emit('enter',this.msg.score)
    },
    handleClick(){
      this.$emit('selectScore',this.msg)
    }
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

.emoji-show {
  animation: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.35) 0.08s forwards emoji_show;
}

@keyframes emoji_show {
  0% {
      transform: translateY(30px);
      opacity: 0;
  }
  100% {
      transform: none;
      opacity: 1;
  }
}

.emoji-item {
  opacity: 0;
}

.emoji-item-img {
  width: 36px;
  height: 36px;
}

.emoji-item-label-content {
  position: relative;
  height: 26px;
}

.emoji-item-label {
  opacity: 0;
  position: absolute;
  top: 4px;
  left: 50%;
  transform: translate(-50%, 5px);
  white-space: nowrap;
  text-align: center;
  font-size: 12px;
  color: #999;
  line-height: 22px;
  transition: opacity, transform 0.3s ease-in-out;
}

.emoji-item:hover .emoji-item-label {
  opacity: 1;
  transform: translate(-50%);
}
</style>
